body .generalWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 100px 400px;
  max-width: inherit;
  width: 70%;
  margin: auto; }
  body .generalWrapper .text {
    margin: 10px 50px; }
  body .generalWrapper .headWrap {
    display: grid;
    grid-column: 1 / 4;
    grid-row: 1 / 2;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 2fr;
    justify-items: center;
    border: 1px solid black; }
    body .generalWrapper .headWrap .orderCountWrap {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      width: 100%; }
    body .generalWrapper .headWrap .cookCountWrap {
      grid-row: 1 / 2;
      grid-column: 3 / 4;
      width: 100%; }
    body .generalWrapper .headWrap .cookScreen {
      grid-row: 2 / 3;
      grid-column: 2 / 3;
      width: 100%;
      border: 3px solid red; }
    body .generalWrapper .headWrap .button {
      margin: 5px; }
    body .generalWrapper .headWrap .addCookBtn {
      grid-row: 2 / 3;
      grid-column: 3 / 4;
      width: 50px; }
    body .generalWrapper .headWrap .deleteCookBtn {
      grid-row: 2 / 3;
      grid-column: 1 / 2;
      width: 50px; }
  body .generalWrapper .mainWrap {
    display: grid;
    grid-column: 1 / 4;
    grid-row: 2 / 3;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center; }
    body .generalWrapper .mainWrap .label {
      text-align: center; }
    body .generalWrapper .mainWrap .queueWrap {
      grid-column: 1 / 2;
      width: 100%;
      border: 1px solid black; }
    body .generalWrapper .mainWrap .processWrap {
      grid-column: 2 / 3;
      width: 100%;
      border: 1px solid black; }
    body .generalWrapper .mainWrap .readyOrderWrap {
      grid-column: 3 / 4;
      width: 100%;
      border: 1px solid black; }
